import { HIDDEN_CLASS, DISABLED_CLASS, SELECTED_CARD_CLASS, TOO_MANY_REQUESTS_CODE, TOO_MANY_REQUESTS_MESSAGE, BAD_REQUEST_CODE, BAD_REQUEST_MESSAGE, } from './constants';
export function show(element) {
    element.classList.remove(HIDDEN_CLASS);
}
export function hide(element) {
    element.classList.add(HIDDEN_CLASS);
}
export function enableButton(button) {
    button.removeAttribute(DISABLED_CLASS);
}
export function disableButton(button) {
    button.setAttribute(DISABLED_CLASS, '');
}
export function capitalize(string) {
    return string[0].toUpperCase() + string.slice(1);
}
export function highlightButton(button) {
    button.classList.add(SELECTED_CARD_CLASS);
}
export function removeHighlightFromButton(button) {
    button.classList.remove(SELECTED_CARD_CLASS);
}
export async function handleRequest({ parentElement, error, loader, request, fetchForecast, handleCardsCreation, }) {
    try {
        hide(error);
        hide(parentElement);
        show(loader);
        const data = await fetchForecast(request);
        hide(loader);
        hide(error);
        show(parentElement);
        handleCardsCreation(data);
    }
    catch (err) {
        hide(loader);
        if (err.message === TOO_MANY_REQUESTS_CODE) {
            error.textContent = TOO_MANY_REQUESTS_MESSAGE;
        }
        if (err.message === BAD_REQUEST_CODE) {
            error.textContent = BAD_REQUEST_MESSAGE;
        }
        show(error);
    }
}
