import { fetchForecast } from './api/fetchForecast';
import { isToday } from 'date-fns';
import { handleRequest, highlightButton } from './utils';
export default function TenDay(onCardButtonClick, setSelectedCardButton, formatDaysData) {
    const loader = document.querySelector('#ten-day-loader');
    const error = document.querySelector('#ten-day-error');
    const forecastElement = document.querySelector('.ten-day-forecast');
    const forecastList = document.querySelector('.ten-day-forecast__list');
    const cardTemplate = document.querySelector('#ten-day-forecast-card-template');
    function createCards(days) {
        days.forEach((day) => {
            const formattedData = formatDaysData(day, { weekday: true });
            const { date, datetime, weekday, dayData, month, icon, conditions, tempmin, tempmax, } = formattedData;
            const clone = cardTemplate.content.cloneNode(true);
            const cardButtonElement = clone.querySelector('.ten-day-forecast__card-button');
            const cardElement = cardButtonElement.querySelector('.ten-day-card');
            const weekdayElement = cardElement.querySelector('.ten-day-card__week-day');
            const dayElement = cardElement.querySelector('.ten-day-card__day');
            const iconElement = cardElement.querySelector('.ten-day-card__icon');
            const highestTempElement = cardElement.querySelector('#ten-day-card-highest-temp');
            const lowestTempElement = cardElement.querySelector('#ten-day-card-lowest-temp');
            weekdayElement.textContent = weekday;
            dayElement.textContent = `${dayData} ${month}`;
            dayElement.dateTime = datetime;
            cardButtonElement.dataset.date = datetime;
            import(`../../assets/img/forecast/${icon}.svg`).then((res) => (iconElement.src = res.default));
            iconElement.alt = conditions;
            highestTempElement.textContent = `${tempmax}`;
            lowestTempElement.textContent = `${tempmin}`;
            if (isToday(date)) {
                setSelectedCardButton(cardButtonElement);
                highlightButton(cardButtonElement);
            }
            forecastList.appendChild(cardButtonElement);
        });
    }
    function handleCardsCreation(data) {
        clear();
        const days = data.days;
        createCards(days);
    }
    function clear() {
        while (forecastList.hasChildNodes()) {
            forecastList.removeChild(forecastList.lastChild);
        }
    }
    async function getForecast(address) {
        const request = {
            address,
            date: 'next9days',
            params: 'include=days&elements=tempmin,tempmax,datetime,icon,conditions',
        };
        handleRequest({
            parentElement: forecastList,
            loader,
            error,
            request,
            fetchForecast,
            handleCardsCreation,
        });
    }
    forecastList.addEventListener('click', onCardButtonClick);
    return { forecastElement, getForecast, clear };
}
