import { fetchForecast } from './api/fetchForecast';
import { setDate, getDay, isToday, format } from 'date-fns';
import { handleRequest, highlightButton } from './utils';
import { DAYS_IN_WEEK } from './constants';
export default function ThirtyDay(today, onCardButtonClick, selectedCardButton, formatDaysData) {
    const loader = document.querySelector('#thirty-day-loader');
    const error = document.querySelector('#thirty-day-error');
    const forecastElement = document.querySelector('.thirty-day-forecast');
    const forecastTable = forecastElement.querySelector('.thirty-day-forecast__table');
    const forecastBody = forecastTable.querySelector('.forecast-table__body');
    const forecastRowNodeList = forecastBody.querySelectorAll('.forecast-table__row');
    const forecastColTemplate = document.querySelector('#forecast-table-col-template');
    const dayOfTheMonth = today.getDate();
    const weekDayIndex = getDay(today);
    const weekDayNormalizedIndex = normalizeWeekDayIndex(weekDayIndex);
    const priorDays = countPriorDays(weekDayNormalizedIndex);
    function countPriorDays(weekDayNormalizedIndex) {
        let priorDays = 0;
        while (priorDays < weekDayNormalizedIndex) {
            priorDays++;
        }
        return priorDays;
    }
    function makeDayColorless(cardElement, classForColorless) {
        cardElement.classList.add(classForColorless);
    }
    function createCards(days) {
        let rowIndex = 0;
        let dayCount = 0;
        const daysBeforeToday = days.slice(0, priorDays);
        days.forEach((day) => {
            dayCount++;
            const formattedData = formatDaysData(day);
            const { date, datetime, dayData, month, icon, tempmin, tempmax, conditions, } = formattedData;
            const clone = forecastColTemplate.content.cloneNode(true);
            const forecastColElement = clone.querySelector('.forecast-table__col');
            const cardButtonElement = forecastColElement.querySelector('.thirty-day-forecast__card-button');
            const cardElement = cardButtonElement.querySelector('.thirty-day-forecast__card');
            const dayElement = cardElement.querySelector('.thirty-day-card__day');
            const iconElement = cardElement.querySelector('.thirty-day-card__icon');
            const highestTempElement = cardElement.querySelector('#thirty-day-card-highest-temp');
            const lowestTempElement = cardElement.querySelector('#thirty-day-card-lowest-temp');
            if (daysBeforeToday.includes(day)) {
                makeDayColorless(cardElement, 'thirty-day-card_colorless');
            }
            dayElement.textContent = `${dayData} ${month}`;
            dayElement.dateTime = datetime;
            cardButtonElement.dataset.date = datetime;
            import(`../../assets/img/forecast/${icon}.svg`).then((res) => (iconElement.src = res.default));
            iconElement.alt = conditions;
            highestTempElement.textContent = `${tempmax}`;
            lowestTempElement.textContent = `${tempmin}`;
            forecastRowNodeList[rowIndex].appendChild(forecastColElement);
            if (isToday(date)) {
                selectedCardButton(cardButtonElement);
                highlightButton(cardButtonElement);
            }
            if (dayCount % DAYS_IN_WEEK === 0)
                rowIndex++;
        });
    }
    function handleCardsCreation(data) {
        clear();
        const days = data.days;
        createCards(days);
    }
    function clear() {
        forecastRowNodeList.forEach((row) => {
            while (row.hasChildNodes()) {
                row.removeChild(row.lastChild);
            }
        });
    }
    function normalizeWeekDayIndex(weekDayIndex) {
        if (weekDayIndex === 0)
            return 6;
        return --weekDayIndex;
    }
    function createDatePeriod(priorDays, upcomingDays) {
        const from = format(setDate(today, dayOfTheMonth - priorDays), 'yyyy-MM-dd');
        const to = format(setDate(today, dayOfTheMonth - priorDays + upcomingDays), 'yyyy-MM-dd');
        const datePeriod = `${from}/${to}`;
        return datePeriod;
    }
    async function getForecast(address) {
        const upcomingDays = 34;
        const date = createDatePeriod(priorDays, upcomingDays);
        const request = {
            address,
            date,
            params: 'include=days&elements=tempmin,tempmax,datetime,icon,conditions',
        };
        handleRequest({
            parentElement: forecastTable,
            loader,
            error,
            request,
            fetchForecast,
            handleCardsCreation,
        });
    }
    forecastBody.addEventListener('click', onCardButtonClick);
    return { forecastElement, getForecast, clear };
}
